'use client';

import { useEffect, useState } from 'react';

interface RepoData {
  repositories: string[];
  count: number;
}

export default function Home() {
  const [data, setData] = useState<RepoData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        const res = await fetch('/api/repos');
        const data = await res.json() as RepoData;
        setData(data);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to fetch');
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-lg text-gray-600 dark:text-gray-400">Loading...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-lg text-red-600">Error: {error}</div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      {/* Summary Stats */}
      <div className="mb-8">
        <p className="text-lg text-gray-600 dark:text-gray-400">
          Tracking {data?.count} repositories across the Prebid ecosystem
        </p>
      </div>

      {/* Main content area - ready for charts */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Left sidebar - Repository list */}
        <div className="lg:col-span-1">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
              Repositories
            </h2>
            <div className="space-y-2">
              {data?.repositories.map(repo => (
                <div
                  key={repo}
                  className="p-3 bg-gray-50 dark:bg-gray-700 rounded-md hover:bg-gray-100 dark:hover:bg-gray-600 transition-colors cursor-pointer"
                >
                  <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                    {repo}
                  </span>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Chart area - placeholder for now */}
        <div className="lg:col-span-2">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6 h-96">
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
              Chart Area
            </h2>
            <div className="flex items-center justify-center h-full bg-gray-50 dark:bg-gray-700 rounded-md">
              <p className="text-gray-500 dark:text-gray-400">
                Charts will be displayed here
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}